import request from '@/utils/request';
var api = {
  GetAll: '/api/dms/DevicePartType/GetAll',
  create: '/api/dms/DevicePartType/create',
  Delete: '/api/dms/DevicePartType/delete',
  UpdateInfo: '/api/dms/DevicePartType/UpdateInfo',
  GetList: '/api/dms/DevicePartType/GetList',
  // GetAllDevice: '/api/dms/DevicePart/GetAll',
  GetById: '/api/dms/DevicePart/GetById',
  GetByDeviceId: '/api/dms/DevicePart/GetByDeviceId',
  UpdateInfoDevice: '/api/dms/DevicePart/UpdateInfo',
  createDevice: '/api/dms/DevicePart/create',
  deleteDevice: '/api/dms/DevicePart/delete',
  BatchAdd: '/api/dms/DevicePart/BatchAdd'
}; // 获取全部设备部件类型

export function GetAll() {
  return request({
    url: api.GetAll,
    method: 'get'
  });
} // 新增设备部件类型

export function create(obj) {
  return request({
    url: api.create,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 删除设备部件类型

export function Delete(id) {
  return request({
    url: api.Delete,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: id
  });
} // 根据关键字模糊查询

export function GetList(obj) {
  return request({
    url: api.GetList,
    method: 'get',
    params: obj
  });
} // 修改设备部件类型

export function UpdateInfo(obj) {
  return request({
    url: api.UpdateInfo,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
} // 获取全部设备部件
