import { UpdateInfo, create } from '@/api/parts';
export default {
  name: 'Details',
  data: function data() {
    return {
      form: {},
      dialogVisible: false,
      type: 'insert',
      valueType: '',
      options: [{
        label: '暂未定义',
        value: 0
      }, {
        label: '开关量',
        value: 1
      }, {
        label: '整数',
        value: 2
      }, {
        label: '浮点数',
        value: 3
      }, {
        label: '字符串',
        value: 4
      }, {
        label: '表达式',
        value: 5
      }, {
        label: '数组',
        value: 6
      }, {
        label: '坐标点',
        value: 7
      }, {
        label: '坐标点数组',
        value: 8
      }]
    };
  },
  methods: {
    init: function init(data) {
      this.dialogVisible = true;

      if (data) {
        this.form = data;
        this.type = 'edit';
      } else {
        this.type = 'insert';
      }
    },
    cancel: function cancel() {
      this.dialogVisible = false;
      this.form = {};
    },
    onSubmit: function onSubmit() {
      var _this = this;

      if (this.type === 'edit') {
        UpdateInfo(this.form).then(function () {
          _this.$message({
            type: 'success',
            message: '修改成功',
            duration: 500,
            onClose: function onClose() {
              _this.$parent.init();

              _this.cancel();
            }
          });
        });
      } else if (this.type === 'insert') {
        create(this.form).then(function () {
          _this.$message({
            type: 'success',
            message: '新增成功',
            duration: 500,
            onClose: function onClose() {
              _this.$parent.init();

              _this.cancel();
            }
          });
        });
      }
    }
  }
};